import React, { useState, useEffect } from "react";
import "./cart_section-js"
import "./Cart.css";
import "../../Component/loader/loader.css";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import $ from 'jquery';
window.$ = $;
window.jQuery = $;



const Cart3 = ({ sectionTitle, category, customImages, useLocalImages, watchimage, watchimage1 }) => {
  const [products, setProducts] = useState([]);
  const [error, setError] = useState(null);
  const [wishlist, setWishlist] = useState({});
  // const [imageLoaded, setImageLoaded] = useState({});
  const [loadedImages, setLoadedImages] = useState({});

  // Load wishlist from localStorage
  useEffect(() => {
    const savedWishlist = JSON.parse(localStorage.getItem("wishlist")) || {};
    setWishlist(savedWishlist);
  }, []);

  // Fetch products from API based on category
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const response = await fetch(`https://dummyjson.com/products/category/${category}`);
        if (!response.ok) throw new Error("Failed to fetch products");
        const data = await response.json();

        // Map and limit products to 8 items
        const mappedProducts = data.products.slice(0, 8).map((product, index) => ({
          id: product.id,
          name: product.title,
          description: product.description,
          price: product.price,
          rating: product.rating,
          category: product.category,
          discount: product.discountPercentage + "%",
          availabilityStatus: product.availabilityStatus || "In Stock",
          images: useLocalImages
            ? customImages(index)  // Use local images if flag is true
            : product.thumbnail || "/Porto/images/default.jpg", // Use API images otherwise
          images1: product.images[0] || "/Porto/images/default.jpg", // Use API images otherwise
          images2: product.images[1] || "/Porto/images/default.jpg", // Use API images otherwise
          images3: product.images[2] || "/Porto/images/default.jpg", // Use API images otherwise
        }));

        setProducts(mappedProducts);
      } catch (err) {
        setError(err.message);
      }
    };

    fetchProducts();
  }, [category, useLocalImages]);

  // Function to toggle wishlist
  const toggleWishlist = (productId) => {
    setWishlist((prevWishlist) => {
      const updatedWishlist = {
        ...prevWishlist,
        [productId]: !prevWishlist[productId],
      };
      localStorage.setItem("wishlist", JSON.stringify(updatedWishlist));
      return updatedWishlist;
    });
  };

  const handleImageLoad = (index) => {
    setLoadedImages((prevState) => ({
      ...prevState,
      [index]: true,
    }));
  };

  if (error) {
    return <div>Error: {error}</div>;
  }

  return (
    <div className="cart3 section">
      <div className="row">
        {products.map((product) => (
          <div className="product" key={product.id}>
            <span className="availabilityStatus">{product.availabilityStatus}</span>
            <div className="product-img">
              <OwlCarousel className='owl-theme' nav items={1} dots={false}>
                {!loadedImages && (
                  <div className="image-loader">
                    <i className="fa-solid fa-compact-disc fa-spin"></i>
                  </div>
                )}
                {[
                  product.images,
                  product.images2,
                  product.images3
                ].map((imgSrc, index) => (
                  <div className="item" key={index}>
                    {!loadedImages[index] && (
                      <div className="image-loader">
                        <i className="fa-solid fa-compact-disc fa-spin"></i>
                      </div>
                    )}
                    <img
                      src={imgSrc}
                      alt={product.name}
                      onLoad={() => handleImageLoad(index)}
                      onError={(e) => (e.target.src = "/Porto/images/default.jpg")}
                      style={{ display: loadedImages[index] ? "block" : "none" }}
                    />
                  </div>
                ))}

              </OwlCarousel>



              <div className="product-label">
                {product.discount && <span className="sale">{product.discount}</span>}
                {product.isNew && <span className="new">NEW</span>}
              </div>
            </div>
            <div className="product-body">
              <p className="product-category">{product.category}</p>
              <h3 className="product-name">
                <a href="#">{product.name}</a>
                <span>{product.description}</span>
              </h3>
              <h4 className="product-price">
                ${product.price}{" "}
                {product.discount && (
                  <del className="product-old-price">
                    ${((product.price / (1 - parseFloat(product.discount) / 100)).toFixed(2))}
                  </del>
                )}
              </h4>
              <div className="product-rating">
                {Array.from({ length: 5 }).map((_, i) => (
                  <i
                    key={i}
                    className={`${i < product.rating ? "fa-solid fa-star" : "fa-regular fa-star"}`}
                  ></i>
                ))}
              </div>
              <div className="product-btns">
                <button className="add-to-wishlist" onClick={() => toggleWishlist(product.id)}>
                  <i className={`${wishlist[product.id] ? "fa-solid" : "fa-regular"} fa-heart`}></i>
                  <span className="tooltipp tooltipp1">add to wishlist</span>
                </button>
                <button className="add-to-compare">
                  <i className="fa fa-exchange"></i>
                  <span className="tooltipp tooltipp2">add to compare</span>
                </button>
                <button className="quick-view">
                  <i className="fa fa-eye"></i>
                  <span className="tooltipp tooltipp3">quick view</span>
                </button>
              </div>
            </div>
            <div className="add-to-cart">
              <button className="add-to-cart-btn">
                <i className="fa fa-shopping-cart"></i> add to cart
              </button>
            </div>
          </div>
        ))}
        <div className="cart_section13_wrapper">
          <img src={watchimage1} alt="" className="cart_section13_img1" />
          <img src={watchimage} alt="" className="cart_section13_img" />
        </div>
      </div>
    </div>
  );
};

export default Cart3;








