import React from "react";
import Cart3 from "./Cart3";
import "./cart_section-js"

const Cart_Section3 = () => {
  const sectionTitle = "Smartphones";
  const category = "smartphones";

  // Define a function to generate local image paths
  const customImages = (index) => `/Porto/images/products_1_${index}.jpg`;

  return (
    <div className="cart_section_title cart_section3">
      <h3>{sectionTitle.replace("-", " ")}</h3>
      <Cart3 
        category={category} 
        customImages={customImages} 
        useLocalImages={true}  // Pass this flag to decide image source
      />
    </div>

  );
};

export default Cart_Section3;
