document.addEventListener("DOMContentLoaded", function () {
  // Check if we are on the Cart Section 3
  var cartSection = document.querySelector(".cart_section3");

  if (cartSection) {
    // Hide all image loaders
    var imageLoaders = cartSection.querySelectorAll(".cart3 .row .product .product-img .image-loader");
    imageLoaders.forEach(function (loader) {
      loader.style.display = "none";
    });

    // Hide all images
    var images = cartSection.querySelectorAll(".cart3 .row .product .product-img img");
    images.forEach(function (img) {
      img.style.display = "none";
    });

    // Show only the first image
    var firstImage = cartSection.querySelector(".cart3 .row .product .product-img img:first-of-type");
    if (firstImage) {
      firstImage.style.display = "block";
    }
  }
});
